  padding: 0;
  margin: 0;
  list-style: none;
  text-decoration: none;
  font-family: monospace;
}

@font-face {
  font-family: bebas;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
  font-family: monserrat;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

body {
  width: 100vw;
  height: 100vh;
  background-color: #363636;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  background-color: #363636;
}

.logo,
.nav-links {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.logo {
  color: #fbbf24;
  font-size: 2rem;
  font-weight: bold;
}

.logo p {
  cursor: pointer;
}

.keys {
  opacity: 0;
  transition: opacity 300ms;
}

.logo:hover .keys {
  opacity: 1;
  cursor: pointer;
}

.nav-links a {
  color: #eaeaea;
  font-weight: bold;
  margin-left: 0.5ch;
  margin-right: 0.5ch;
}

.sel {
  color: #eaeaea;
  font-weight: bold;
  opacity: 0;
  transition:
    opacity 200ms,
    color 200ms;
}

li:hover .sel {
  opacity: 1;
  color: #fbbf24;
}

/* Main section */
main {
  display: grid;
  padding: 2rem;
}

section,
header {
  padding: 2rem;
}

/* Header */
header {
  display: grid;
  grid-template-columns: 300px 1fr;
  grid-template-rows: 350px 1fr;
  align-items: center;
  column-gap: 3rem;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: cover;
  background-position: 50% 55%;
  border-radius: 3rem;
  color: #eaeaea;
}

.name {
  color: #fbbf24;
  font-size: 4rem;
}

.cheers h1 {
  font-size: 3rem;
}

.dev-cont {
  display: flex;
  align-items: center;
  margin-top: 2rem;
  gap: 1rem;
}

.dev-detail {
  font-size: 1.5rem;
  font-weight: bold;
  animation: alter 1s alternate ease-in-out infinite;
}

@keyframes alter {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.dev {
  font-size: 1.5rem;
  font-weight: bold;
}

/* About Me*/
.aboutMe {
  grid-row: 1 / 3;
  justify-self: baseline;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.title-cont {
  position: relative;
  display: inline-block;
  max-width: 100%;
}

.about-title {
  position: absolute;
  top: 55%;
  left: 60%;
  font-size: 2rem;
  font-weight: bold;
  color: #363636;
  padding: 1rem;
  background-color: #eaeaea80;
  backdrop-filter: blur(10px);
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
  text-align: center;
}

.text-about {
  box-sizing: border-box;
  max-width: 300px;
  text-align: center;
  font-size: 1.2rem;
  color: #eaeaea;
  background: linear-gradient(#363636, #fbbf2470);
  padding: 2rem;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  font-family: monserrat;
  font-weight: 500;
}

.benja {
  max-width: 300px;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}

/* proyects */
.proyects {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  z-index: 0;
}

.proyect-t {
  font-size: 3rem;
  font-weight: bold;
  color: #fbbf24;
}

.p-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 4rem;
}

.card {
  display: flex;
  align-items: end;
  max-width: 300px;
  height: 350px;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  background-size: cover;
}

.p-info {
  display: flex;
  flex-direction: column;
  align-items: end;
  gap: 1.5rem;
  padding: 2rem;
  font-size: 0.8rem;
  background-color: #17171799;
  backdrop-filter: blur(10px);
  border-top-left-radius: 150px;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
}

.p-title {
  font-weight: bold;
  font-size: 1.2rem;
  color: #fbbf24;
}

.p-info {
  text-align: end;
  color: #eaeaea;
  font-weight: 500;
}

.weather {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  background-position: center;
}

.todo {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.library {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

.battleship {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
  background-position: center;
}

.dashboard {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
}

.pong {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
}

/* contact Me */
.contact {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  background-color: #fbbf24;
  color: #363636;
  border-radius: 1rem;
  z-index: 1;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  max-width: 500px;
  font-size: 1.5rem;
}

.contact-me {
  font-size: 3rem;
  font-weight: bold;
}

.img-contact {
  max-width: 300px;
  border-radius: 1rem;
}

.contact-info ul li {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  background-color: #363636;
  color: #fbbf24;
  font-weight: bold;
}

@media (max-width: 1168px) {
  header {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }

  .cheers {
    grid-row: 1;
  }

  .aboutMe {
    grid-row: 2;
  }
}

@media (max-width: 628px) {
  .cheers,
  .aboutMe {
    justify-self: center;
  }

  .cheers h1,
  .name {
    font-size: 2rem;
  }

  .dev,
  .dev-detail {
    font-size: 1rem;
  }
}

@media (max-width: 550px) {
  .a {
    display: none;
  }
  nav {
    justify-content: center;
  }

  main,
  section,
  header {
    padding: 0;
  }
}
